import Web3 from 'web3'
import { BigNumber } from 'bignumber.js'
import { toBN, BN, isBN } from 'web3-utils'

const dood_abi = require('./abis/dood.json')

// eth
const web3Provider = 'https://mainnet.infura.io/v3/9aa3d95b3bc440fa88ea12eaa4456161'
const web3Address = '0x97d702037ca1d3ab3ad649248c3612935b60f4f4'

// ropsten
//const web3Provider = 'https://ropsten.infura.io/v3/9aa3d95b3bc440fa88ea12eaa4456161'
//const web3Address = '0xdEeE77bb1DE6578741017A91C97baE6f1d90e203'

export class Dood {
	constructor() {
		this.web3 = new Web3(window.ethereum)
		this.web3Reader = new Web3(new Web3.providers.HttpProvider(web3Provider))

		this.contractReader = new this.web3Reader.eth.Contract(
			dood_abi,
			web3Address
		)

		this.web3.eth.getGasPrice().then((result) => {
			self.defaultGasPrice = result
			console.log(result)
		})

		if (this.web3 != null) {
			this.contract = new this.web3.eth.Contract(
				dood_abi,
				web3Address
			)
		}
	}

	async totalSupply() {
		return this.contractReader.methods.totalSupply().call()
	}

	async isActive() {
		return this.contractReader.methods.isActive().call()
	}

	async userMintCount(account) {
		return this.contractReader.methods.userMinted(account).call()
	}

	async freeMintCount() {
		return this.contractReader.methods.freeMintCount().call()
	}

	async mint(account, count, callback) {
		var gasPrice = this.defaultGasPrice

		let weiAmount = BigNumber(count * 35).times(BigNumber(10).pow(BigNumber(15)));
		let weiAmountBN = toBN(weiAmount)

		//console.log(this.web3Reader.toWei('0.035', 'ether'))

		var tx = this.contract.methods.mint(count)
		var gasLimit = await tx.estimateGas({ value: weiAmountBN, from: account, to: web3Address})
		return tx.send({
			from: account,
			value: weiAmountBN,
			gasPrice: gasPrice,
			gas: Math.round(gasLimit * 1.1)
		}, callback)
	}

}

export default Dood